  border: 0;
  border-radius: 0.125rem;
  cursor: pointer;
  text-transform: uppercase;
  white-space: normal;
  word-wrap: break-word;
  color: #ffffff !important;
}

.btn:hover,
.btn:active,
.btn:focus {
  outline: 0;
}

.btn .fa {
  position: relative;
  font-size: 0.9rem;
}

.btn .fa.right {
  margin-left: 0.3rem;
}

.btn .fa.left {
  margin-right: 0.3rem;
}

.btn.btn-lg {
  padding: 1rem 2.4rem;
  font-size: 0.94rem;
}

.btn.btn-lg .fa {
  font-size: 1rem;
}

.btn.btn-md {
  padding: 0.7rem 1.6rem;
  font-size: 0.7rem;
}

.btn.btn-md .fa {
  font-size: 0.8rem;
}

.btn.btn-sm {
  padding: 0.5rem 1.6rem;
  font-size: 0.64rem;
}

.btn.btn-sm .fa {
  font-size: 0.7rem;
}

.btn.btn-tb {
  padding: 0.3rem 1rem;
}

.btn.btn-block {
  margin: inherit;
}

.btn.btn-link {
  box-shadow: none;
  background-color: transparent;
}

.btn.btn-link:active,
.btn.btn-link:focus,
.btn.btn-link:hover {
  box-shadow: none !important;
  background-color: transparent;
}

.btn[class*="btn-outline-"] {
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
}

.btn[class*="btn-outline-"].btn-lg {
  padding-top: 0.88rem;
  padding-bottom: 0.88rem;
}

.btn[class*="btn-outline-"].btn-md {
  padding-top: 0.58rem;
  padding-bottom: 0.58rem;
